
.zip-label {
    font-size: 50px;
    font-weight: bold;
    font-family: 'Open Sans', sans-serif;
    color: white;
    text-shadow: 2px 2px 6px #000000;
}

@media only screen and (max-width : 320px) {
    .zip-form {
        max-width: 60%;
    }
}

.zipcode {
    width:20%;
}

.home-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.form-cont {
    display:flex;
    max-width: 95%;
    padding-bottom: 50px;
}

.home-container .zipform {
    margin-bottom: 0px;
}

html body .zip-sub {
    align-self: flex-end;
    background-color: #63bd45;
    border-color: #63bd45;
    box-shadow: 2px 2px grey;
}

.home {
    background-image: url(/sourcing-boston/static/media/aubergines-bio-cabbage-5205.ff454842.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}
html body .food-nav {
    background-color: #63bd45;
    position: fixed;
    width: 100%;
    top: 0;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    z-index: 100;
}

html body .food-link {
    color: white !important;
}

.navbar-nav {
    margin-left:auto;
}
.schedule-link {
    color: white !important;
    margin: 0px auto;
    text-transform: uppercase;
    font-size: 1.25rem;
    line-height: inherit;
}

.row {
    min-height: 200px;
}


